.membership-page {
  padding: 20px;
  text-align: center;
}

.plans-container {
  display: flex;
  justify-content: center;
  gap: 20px;
  flex-wrap: wrap;
}

.plan-card {
  border: 1px solid #ccc;
  border-radius: 12px;
  padding: 20px;
  width: 200px;
  background: #f9f9f9;
  transition: transform 0.3s;
}

.plan-card.selected {
  border-color: #007bff;
  transform: scale(1.05);
  background: #e9f5ff;
}

.plan-card h3 {
  margin-bottom: 5px;
}

.plan-card ul {
  list-style: none;
  padding: 0;
  margin: 10px 0;
}

.plan-card button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 8px 12px;
  border-radius: 6px;
  cursor: pointer;
}

.payment-section {
  margin-top: 30px;
}

.pay-btn {
  background: #28a745;
  color: white;
  padding: 10px 18px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}
