.filters-sidebar {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 1rem;
  padding: 20px;
  background-color: white;
  border-radius: 8px;
  margin: 10px;
}

.filters-sidebar h4 {
  margin: 0;
  font-size: 1.1rem;
  flex-shrink: 0;
}

.filters-sidebar label {
  margin-right: 0.5rem;
  font-weight: 500;
}

.filters-sidebar select {
  padding: 6px 12px;
  border: 1px solid #ccc;
  border-radius: 6px;
  background-color: white;
  min-width: 140px;
}
