.sidenav {
  height: 100vh;
  width: 220px;
 background-color: #f9fefb;
  border-right: 1px solid #d1fae5;
  box-shadow: 2px 0 10px rgba(0, 0, 0, 0.08);
  border-top-right-radius: 16px;
  border-bottom-right-radius: 16px;
  transition: all 0.3s ease;
  padding-top: 20px;
  position: fixed;
  top: 0;
  left: 0;
  transition: width 0.3s ease;
  overflow-x: hidden;
  box-shadow: 2px 0 5px rgba(0,0,0,0.05);
  z-index: 1000;
}
 
.sidenav::-webkit-scrollbar {
  display: none; /* Chrome, Safari, Edge */
}
.sidenav.closed {
  width: 70px;
}

.sidenav-logo {
  font-size: 20px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 30px;
  color: #16a34a;
  white-space: nowrap;
}

.sidenav-links {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding-left: 10px;
}

.sidenav-link {
  display: flex;
  align-items: center;
  padding: 10px 14px;
  border-radius: 8px;
  color: #16a34a;
  text-decoration: none;
  font-size: 16px;
  transition: background 0.2s, color 0.2s;
}

.sidenav-link:hover {
  background-color: #dcfce7;
  color: #065f46;
}

.sidenav-link .icon {
  font-size: 20px;
  margin-right: 10px;
}

.sidenav.closed .sidenav-link .label {
  display: none;
}

.sidenav.closed .sidenav-link {
  justify-content: center;
}
