


.search-bar {
  position: relative;
  width: 100%;
  border-radius: 8px;
  max-width: 400px;
}
.search-bar input {
  width: 100%;
  padding: 8px 12px;
  border-radius: 4px;
  border: 1px solid  #2e7d32;
}
.suggestions {
  position: absolute;
  background: white;
  border: 1px solid #ccc;
  width: 100%;
  z-index: 10;
  list-style: none;
  max-height: 200px;
  overflow-y: auto;
}
.suggestions li {
  display: flex;
  align-items: center;
  padding: 6px 10px;
  cursor: pointer;
}
.suggestions li img {
  width: 30px;
  height: 30px;
  object-fit: cover;
  margin-right: 10px;
}
.suggestions li:hover {
  background-color: #f1f1f1;
}
