/* Cart Container - vertical layout */
.cart-container {
  display: flex;
  flex-direction: column;
  gap: 30px;
  padding: 24px;
  max-width: 1100px;
  margin: 0 auto;
  background-color: #f8f9fa;
  font-family: 'Segoe UI', sans-serif;
}

/* Cart Items Section */
.cart-left {
  background-color: #fff;
  border-radius: 10px;
  padding: 24px;
}

.cart-left h2 {
  margin-bottom: 10px;
  font-size: 24px;
}

.cart-left p {
  margin-bottom: 20px;
  color: #666;
}

/* Cart Table Styling */
.cart-table {
  width: 100%;
  border-collapse: collapse;
}

.cart-table th,
.cart-table td {
  padding: 14px;
  text-align: center;
  border-bottom: 1px solid #ddd;
  font-size: 15px;
}

.cart-table th {
  background-color: #f1f1f1;
}

.cart-img {
  width: 60px;
  height: 60px;
  object-fit: cover;
  border-radius: 6px;
  margin-right: 12px;
}

.product-info {
  display: flex;
  align-items: center;
  gap: 10px;
}

/* Quantity buttons */
.qty {
  margin: 0 10px;
  font-weight: bold;
}

button {
  padding: 6px 12px;
  border: none;
  background-color: #38a169;
  color: white;
  font-weight: 600;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #2f855a;
}

/* Right Panel Section */
.cart-right {
  background-color: #fff;
  border-radius: 10px;
  padding: 24px;
  display: flex;
  flex-direction: column;
  gap: 24px;
}

/* Boxes inside cart-right */
.shipping-box,
.coupon-box,
.payment-box,
.summary-box {
  padding: 16px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #fafafa;
}

.shipping-box h4,
.coupon-box h4,
.payment-box h4,
.summary-box h4 {
  margin-bottom: 10px;
  font-size: 18px;
  color: #333;
}

input,
select {
  width: 100%;
  padding: 10px 12px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 14px;
}

/* Summary section styling */
.summary-box p {
  display: flex;
  justify-content: space-between;
  margin: 6px 0;
  font-size: 15px;
}

.summary-box .grand-total {
  border-top: 1px solid #ccc;
  margin-top: 10px;
  padding-top: 10px;
  font-size: 18px;
  font-weight: bold;
}

.summary-box button {
  width: 100%;
  padding: 12px;
  font-size: 16px;
  margin-top: 16px;
  background-color: #e53e3e;
}

.summary-box button:hover {
  background-color: #c53030;
}

/* Responsive (Mobile/Tablet) */
@media screen and (max-width: 768px) {
  .cart-container {
    padding: 16px;
  }

  .cart-img {
    width: 45px;
    height: 45px;
  }

  button {
    padding: 6px 10px;
    font-size: 14px;
  }

  .summary-box button {
    font-size: 14px;
  }
}
/* Gift Wrap Box Enhancements */
.box {
  margin-top: 20px;
  padding: 15px;
  background: white;
  border: 1px solid #ddd;
  border-radius: 8px;
}

.box h4 {
  margin-bottom: 10px;
  font-size: 16px;
  font-weight: bold;
}

.box label {
  display: flex;
  align-items: right;
  gap: 10px;
  margin-left: -600px;
  margin-top: -25px;
  font-size: 14px;
  color: black;
}

.box input[type="checkbox"] {
  transform: scale(1.2);
  margin-right: 8px;
  cursor: pointer;
}

.box textarea {
  width: 100%;
  margin-top: 10px;
  padding: 12px;
  font-size: 14px;
  border-radius: 6px;
  border: 1px solid #ccc;
  resize: vertical;
  min-height: 80px;
}
