/* ====== General Header Styling ====== */
.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(8px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.06);
  padding: 10px 30px;
  z-index: 1000;
  border-bottom: 1px solid #e2e8f0;
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.sidebar-logo {
  margin-left: -30px;
  margin-top: -20px;
  height: 80px;
  width: 240px;
}

.menu-btn {
  background: transparent;
  border: none;
  border-radius: 50%;
  font-size: 20px;
  width: 42px;
  height: 42px;
  color:  #2e7d32;
  cursor: pointer;
  margin-left: -20px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.2s ease;
}

.menu-btn:hover {
  transform: scale(1.05);
  color: white;
}

/* ====== Search ====== */
.search {
  flex: 0.5;
  max-width: 500px;
  padding: 10px 16px;
  font-size: 1rem;
  border-radius: 12px;
  border: 1px solid #ccc;
  background-color: #f9f9f9;
  transition: all 0.3s ease;
}

.search:focus {
  outline: none;
  border-color: #86d993;
  background-color: #fff;
}

/* ====== Header Actions (Right Side Icons) ====== */
.header-actions {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-right: 30px;
}

.header-actions i {
  font-size: 20px;
  border-radius: 50%;
  padding: 10px;
  color:  #2e7d32;
  cursor: pointer;
  transition: background 0.3s ease, transform 0.2s ease;
}

.header-actions i:hover {
  background: #d2ffe4;
  transform: scale(1.1);
}

/* ====== Cart ====== */
.cart-link {
  position: relative;
  color: #333;
  text-decoration: none;
}

.cart-count {
  position: absolute;
  top: -5px;
  right: -10px;
  background-color: #ff5e57;
  color: white;
  border-radius: 50%;
  padding: 2px 6px;
  font-size: 12px;
}

/* ====== Profile and Membership ====== */
.profile-info {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-right: -50px;
  color: #333;
  text-decoration: none;
}

.profile-info img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
}

.membership-link {
  margin-left: 10px;
  text-decoration: none;
}

.membership {
  font-size: 13px;
  background: #e6f0ff;
  color:  #2e7d32;
  padding: 6px 12px;
  border-radius: 12px;
  font-weight: 600;
  transition: background 0.3s;
}

.membership:hover {
  background: #d0e3ff;
}

/* ====== Notification Dropdown ====== */
.notifications-dropdown {
  position: absolute;
  top: 70px;
  right: 100px;
  background: white;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
  padding: 12px;
  border-radius: 10px;
  width: 220px;
  z-index: 1001;
  animation: fadeIn 0.3s ease;
}

.notifications-dropdown ul {
  padding: 0;
  margin: 8px 0 0;
  list-style: none;
}

.notifications-dropdown li {
  padding: 8px 0;
  font-size: 14px;
  border-bottom: 1px solid #eee;
}

.notifications-dropdown li:last-child {
  border-bottom: none;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* ====== Dark Mode Styles ====== */
body.dark-mode {
  background-color: #121212;
  color: #f0f0f0;
}

body.dark-mode .header {
  background: rgba(30, 30, 30, 0.95);
  border-bottom: 1px solid #333;
}

body.dark-mode .search {
  background: #2a2a2a;
  color: #fff;
  border-color: #444;
}

body.dark-mode .search:focus {
  border-color: #7ee787;
}

body.dark-mode .header-actions i {
  background-color: #1f1f1f;
  color: #eee;
}
body.dark-mode .language-selector {
  background-color: #1f1f1f;
  color: #eee;
}
body.dark-mode .menu-btn {
  background-color: #1f1f1f;
  color: #eee;
}

body.dark-mode .notifications-dropdown {
  background: #2c2c2c;
  color: #ddd;
}

body.dark-mode .membership {
  background-color: #1f2937;
  color: #86efac;
}


/* Language selector */
.language-selector {
  position: relative;
  display: flex;
  flex-direction: column;
}

.language-button {
  display: flex;
  align-items: center;
  gap: 6px;
  cursor: pointer;
  padding: 10px;
  border-radius: 30px;
  font-size: 14px;
  color: #333;
  transition: background 0.3s ease;
}

.language-button:hover {
  background: #d2ffe4;
}

.language-dropdown {
  position: absolute;
  top: 50px;
  right: 0;
  background: white;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  overflow: hidden;
  width: 140px;
  z-index: 10;
}

.language-dropdown li {
  padding: 10px;
  font-size: 14px;
  cursor: pointer;
  transition: background 0.2s;
}

.language-dropdown li:hover {
  background: #f0f0f0;
}

/* Notification wrapper fix */
.notification-wrapper {
  position: relative;
}

.search-container {
  position: relative;
  width: 100%;
  max-width: 320px;
  margin: 0 20px;
}

.search-input {
  width: 100%;
  padding: 10px 40px 10px 12px;
  border: 1px solid #ccc;
  border-radius: 20px;
  font-size: 14px;
  transition: 0.2s;
}

.search-input:focus {
  outline: none;
  border-color: #22c55e;
  box-shadow: 0 0 0 2px rgba(34, 197, 94, 0.2);
}

.search-icon {
  position: absolute;
  right: 14px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 16px;
  color: #888;
  pointer-events: none;
}
